async  pagar2(op, titulo) {

  // preguntar como desea pagar

  const modal = await this.modalController.create({
    component: MyModalcalcuPage,
    animated: true,
    componentProps:  {moneda: this.moneda, title: 'Ingrese monto' , botonn: titulo , botonn2: titulo, validarsaldo: 'si' },

    presentingElement: await this.modalController.getTop() , cssClass: 'modal-fullscreen'
  });
  modal.onWillDismiss().then((data) => {

    if ( data.data.data !== 'close' ) {

            this.pagarconqr(data.data.data, op, titulo);



      }


  });
  return await modal.present();


}


async  pagarconqr(monto, estado, titulo) {
  // 'pagogeneraqr'
  const modal = await this.modalController.create({
    component: MyModalpayPage,
    animated: true,
    componentProps:  {moneda: this.moneda, title: titulo  , monto : monto, tipo: estado, userntrasferencia:this.toUser.name , usertrasferencia:this.toUser.email},

    presentingElement: await this.modalController.getTop() , cssClass: 'modal-fullscreen'
  });
  modal.onWillDismiss().then((data) => {

    if ( data.data.data !== 'close' ) {
        var  cad=  (data.data.data).split('°')
          this.foto1img =  data.data.img;
          this.image   		=  data.data.img;
          this.editorMsg="Transferencia de " + this.moneda + " " + cad[4]
           this.sendMsg2();
           // this.cobrarconqr(data.data);



      }


  });
  return await modal.present();

}




