async takePicture(op) {
  const image = await Plugins.Camera.getPhoto({
    correctOrientation: true,
    height: 100,
    width: 100,
        quality: 40,
    allowEditing: true,
    resultType: CameraResultType.DataUrl,
    source: CameraSource.Camera
  });
  this.authenticationService.generateFromImage(image.dataUrl, 100, 100, 0.5, data => {
    switch (op) {
      case 'foto1a': this.foto1img = data; 
      this.image   		= image.dataUrl;
      this.sendMsg2();
      break;
     
        default:
    }
});
}
uploadPWA2(op , controlx) {
  const fileList: FileList = controlx;
  if (fileList && fileList.length > 0) {
    this.firstFileToBase64(fileList[0]).then((result: string) => {
      this.authenticationService.generateFromImage(result, 150, 150, 0.5, data => {
        switch (op) {
          case 'foto1a':  this.foto1img = data;
          this.image   		= data;
          this.sendMsg2();
          break;

           break;
            default:
        }
      });
    }, (err: any) => {
      switch (op) {
        case 'foto1a':  this.foto1img = null; break;
  
          default:
      }
    });
  }
}
uploadPWA(op) {
  switch (op) {
    case 'foto1a':  if (this.foto1a == null ) { return; }
    this.uploadPWA2(op , this.foto1a.nativeElement.files); break;

      default:
  }
}
private firstFileToBase64(fileImage: File): Promise<{}> {
  return new Promise((resolve, reject) => {
    const fileReader: FileReader = new FileReader();
    if (fileReader && fileImage != null) {
      fileReader.readAsDataURL(fileImage);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    } else {
      reject(new Error('No file found'));
    }
  });
}
async takePicture2(op) {
  if ( op === 'foto1a')  {     this.foto1a.nativeElement.click();  }
 
}
