
import { Component, OnInit, ViewChild, ElementRef, NgZone } from '@angular/core';
import { FormGroup, FormControl, Validators, FormGroupDirective } from '@angular/forms';
import { ModalController , NavParams, IonRadioGroup } from '@ionic/angular';
import { AuthenticationService } from '../../services/Authentication.service';
import { TranslateService } from '@ngx-translate/core';



@Component({
  selector: 'app-my-modalcredito',
  templateUrl: './my-modalcredito.page.html',
  styleUrls: ['./my-modalcredito.page.scss'],
})
export class MyModalcreditoPage implements OnInit {
	@ViewChild('radioGroup') radioGroup: IonRadioGroup
	Form: FormGroup;
  title = 'AGM project';
  passcode: any;
  pageStatus: any;
  codeone: any;
  codetwo: any;
  codethree: any;
  codefour: any;
  int: any;
  items2: any  = [] ;
  newPincount: any;
  message: any;
  mypinseguro: any;
  finalPin: any;
  tpagoid:any;
  fingerPin: any;
  tituloxx: any;
  msg = '';
  seguro: any;
  metodopago: any;
  userinfo: any = [];

  items: any  = [] ;
  defaultSelectedRadio = "efectivo";

itemsList: Array<any>;

  public cards: any;

  @ViewChild('search', {static: false})
  public searchElementRef: ElementRef;


  constructor( public translateService: TranslateService    , private modalController: ModalController, private authenticationService: AuthenticationService,
    private navParams: NavParams
  ) {
	this.userinfo = this.authenticationService.getcopiauser();
    this.passcode = '';
	  this.finalPin = '';
	  this.mypinseguro = this.userinfo[0].codpersonal;
  	this.message = true;
  	this.pageStatus = 'Enter Pin'
  	this.int = 0;
  	this.newPincount = 0;
	this.fingerPin = false;


  }

  padLeadingZeros(num, size) {
    let s = num +'';
    while (s.length < size) { s = "0" + s; }
    return s;
}
 ngOnInit() {

	this.tituloxx = this.navParams.get('title');
this.metodopago = this.navParams.get('metodopago');
this.userinfo = this.authenticationService.getcopiauser();

this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value;  });
this.translateService.get('MSGM').subscribe((value) => {   this.msg = value;  });
//this.radioGroup.value = 'efectivo'

this.pideregistros('Activo');



 }

 ngAfterViewInit() {    setTimeout(() => {      this.selectTwo();  }, 400); }

pagar() {
	this.modalController.dismiss({data: this.metodopago, item: this.tpagoid});
}
  async closeModal() {
    this.modalController.dismiss({data: 'close'});
  }


  selectTwo() {
    this.radioGroup.value = 'efectivo'
  }
  pideregistros(op) {
    this.itemsList = [];
    
  
    this.authenticationService.getReg('todos/' + op + '/' + this.userinfo[0].idempresa._id + '/' +  this.userinfo[0]._id, 'tarjetacreditos', 0).then((data) => {
 
    this.items =  data;
    this.itemsList = this.items;
}, (err) => {
this.authenticationService.presentAlert(err.error , this.msg , '');
});
}

radioSelect(event) {
   this.tpagoid= event.detail.value
 
  }


}
